    .App {
        text-align: center;
        min-height: 100vh;
        min-width: 100vw;
        max-width: 100vw;
    }
    
    .gradient {
        background: linear-gradient(-90deg, transparent, black, black, transparent);
        background-size: 500% 500%;
        -webkit-animation: Gradient 20s ease infinite;
        -moz-animation: Gradient 20s ease infinite;
        animation: Gradient 20s ease infinite;
        z-index: 2
    }
    
    @keyframes Gradient {
        0% {
            background-position: 0% 50%
        }
        50% {
            background-position: 100% 50%
        }
        100% {
            background-position: 0% 50%
        }
    }
    
    html {
        height: 100%;
    }
    
    body {
        // background: #222222;
        box-shadow: inset 0 0 400px #111111;
        height: auto;
        max-width: auto;
        // overflow: hidden;
        position: relative;
        z-index: 2;
        overflow-x: hidden;
    }
    
    .rain {
        background: white;
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
        height: 50px;
        position: absolute;
        width: 1px;
        opacity: .02;
        z-index: 0;
    }
    
    $rain: 150;
    @for $i from 1 through $rain {
        $top: (random(35) + 35) * 1%;
        $left: random(100) * 1%;
        $opacity: (random(30) + 30) * 0.01;
        $delay: random(20) - 1s;
        .rain:nth-of-type(#{$i}) {
            animation-name: rain-#{$i};
            animation-delay: $delay;
            animation-duration: random(6) + 4s;
            animation-iteration-count: infinite;
            left: $left;
            opacity: $opacity;
            top: -$top;
        }
        @keyframes rain-#{$i} {
            0% {
                left: $left;
                opacity: $opacity;
                top: -$top;
            }
            100% {
                opacity: 0;
                top: $top + 20%;
            }
        }
    }
    
    .test {
        width: 100vw;
        // height: 90vh;
        color: #fff;
        background: linear-gradient(1deg, rgba(238, 119, 82, .1), rgba(181, 26, 26, 0.2), rgba(35, 166, 255, .2), rgba(45, 128, 109, 0.3));
        background-size: 400% 400%;
        // -webkit-animation: Gradient 15s ease infinite;
        // -moz-animation: Gradient 15s ease infinite;
        animation: Gradient 15s ease-in-out infinite;
    }
    
    .stars {
        animation: Opacity 15s ease-in-out infinite;
    }
    
