    @keyframes Gradient {
        0% {
            background-position: 0% 20%
        }
        50% {
            background-position: 100% 100%
        }
        100% {
            background-position: 0% 20%
        }
    }
    
    @keyframes Opacity {
        10% {
            opacity: .3
        }
        50% {
            opacity: .5
        }
        100% {
            opacity: 1
        }
    }
    
