    .figure-img {
        border-radius: 10px;
    }
    
    .figure {
        margin: 2%;
        z-index: 100;
        filter: drop-shadow(3px 3px 10px black);
        cursor: pointer;
    }
    
    .authButtons:hover {
        transform: scale(1.1);
        opacity: 1;
        transition-duration: .5s;
        background-color: lightgrey;
        color: black;
        filter: drop-shadow(7px 7px 17px black);
    }
    
    .authfield1 {
        background-color: transparent;
        color: white;
        border: none;
        text-align: center;
        outline: none !important;
    }
    
    .authfield1:-webkit-autofill,
    .authfield1:-webkit-autofill:hover,
    .authfield1:-webkit-autofill:focus,
    .authfield1:-webkit-autofill:active {
        transition: background-color 5000s ease-in-out 0s;
        color: white;
        -webkit-text-fill-color: white !important;
    }
    
    .line {
        margin-bottom: 10%;
        margin-top: 0 !important;
    }
    
    .authfield2 {
        background-color: transparent !important;
        color: white !important;
        border: none !important;
        border-bottom: 1px solid white !important;
        text-align: center !important;
        outline: none !important;
    }
    
    .authButtons {
        padding: 2%;
        margin-bottom: 5%;
        background-color: transparent;
        color: white;
        border: solid 1px white;
        border-radius: 5px;
        opacity: .7;
        filter: drop-shadow(5px 5px 5px black);
        outline: 'none';
        user-select: none;
        margin-top: 5%;
        font-size: 1rem;
    }
    /* width */
    
     ::-webkit-scrollbar {
        width: 5px;
        border-radius: '10px';
        position: absolute;
    }
    /* Track */
    
     ::-webkit-scrollbar-track {
        background: transparent;
        border-radius: '50px';
    }
    /* Handle */
    
     ::-webkit-scrollbar-thumb {
        background: #888;
        border-radius: 20px;
        height: 5px;
    }
    /* Handle on hover */
    
     ::-webkit-scrollbar-thumb:hover {
        background: #555;
        color: 'pink';
    }
    
     ::-webkit-scrollbar-track-piece {
        background: transparent;
    }
    
    .iconImage {
        user-select: none;
    }